.Home {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  color: rgba(255, 255, 255, 0.99);
  width: 90%;
}

.HomeIcons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
  width: 10%;
  margin-right: 5vw;
}

.HomeIcons a {
  color: rgba(255, 255, 255, 0.99);
}

.HomeIcons ul {
  list-style: none;
  color: rgba(255, 255, 255, 0.99);
}

.HomeIcons ul li {
  margin: 2vh 0;
}

.HomeMain {
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  background: -webkit-linear-gradient(135deg, rgba(0, 0, 0, 0.8), rgba(5, 60, 92, 1)),
    url("https://images.pexels.com/photos/34676/pexels-photo.jpg?auto=compress&cs=tinysrgb&h=650&w=940")
      no-repeat;
  background: -o-linear-gradient(135deg, rgba(0, 0, 0, 0.8), rgba(5, 60, 92, 1)),
    url("https://images.pexels.com/photos/34676/pexels-photo.jpg?auto=compress&cs=tinysrgb&h=650&w=940")
      no-repeat;
  background: linear-gradient(-45deg, rgba(0, 0, 0, 0.8), rgba(5, 60, 92, 1)),
    url("https://images.pexels.com/photos/34676/pexels-photo.jpg?auto=compress&cs=tinysrgb&h=650&w=940")
      no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  display: flex;
}
.Home h1 {
  margin-left: 10vw;
  font-size: 3em;
  letter-spacing: 0.2vw;
  margin-bottom: 1px;
  width: 100%;
}

.Home h3 {
  margin: 2vh 0 2vh 10vw;
  font-size: 2em;
  font-weight: 400;
  width: 100%;
}

.Home h5 {
  width: 100%;
  text-align: center;
  line-height: 40px;
  margin-top: 0;
  background: -webkit-gradient(linear, right top, left top, from(#f06449), to(#ef3636));
  background: -webkit-linear-gradient(right, #f06449, #ef3636);
  background: -o-linear-gradient(right, #f06449, #ef3636);
  background: linear-gradient(270deg, #f06449, #ef3636);
  cursor: pointer;
}

.Home a {
  color: rgba(255, 255, 255, 0.99);
  text-decoration: none;
  margin-left: 10vw;
  width: 10vw;
}

.HomeIcons a:hover {
  color: #ef3636;
  -webkit-transition-duration: 0.8s;
       -o-transition-duration: 0.8s;
          transition-duration: 0.8s;
  -webkit-transition-timing-function: ease-out;
       -o-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

@media only screen and (max-width: 1200px) {
  .Home a {
    width: 20vw;
  }
}
@media only screen and (max-width: 768px) {
  .Home {
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    width: 100%;
    margin-left: 0;
  }
  .Home h1,
  .Home h3 {
    margin: 2vh 0;
  }
  .Home a {
    width: 30vw;
    margin-left: 0;
    margin-top: 2vh;
  }
  .HomeMain {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }

  .HomeIcons ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    margin-top: 3vh;
  }
  .HomeIcons ul li {
    margin: 0 2vw;
  }
  .HomeIcons ul li a {
    font-size: 0.8em;
  }
}

@media only screen and (max-width: 600px) {
  .Home h1 {
    font-size: 2.5em;
  }
  .Home h3 {
    font-size: 1.5em;
  }
}



