.About {
  height: 40vh;
  background: #1e1c1c;
  display: -ms-flexbox;
  display: flex;
  color: rgba(255, 255, 255, 0.95);
}

.text {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  height: 100%;
}

.text h2 {
  margin: 0;
  font-size: 3em;
}

.text h4 {
  margin-left: 2vw;
  font-weight: 400;
}

.Me {
  margin: 0;
  color: rgb(60, 169, 255);
}

.text ul {
  width: 90%;
  list-style: none;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0;
}

.text ul li {
  color: rgba(255, 255, 255, 0.95);
  margin: 1vh 0;
  border: 1px solid rgb(60, 169, 255);
  width: 10vw;
  padding: 7px 0;
  margin: 1vh 0.2vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

@media only screen and (max-width: 1200px) {
  .text ul li {
    width: 20vw;
  }
}
@media only screen and (max-width: 768px) {
  .About {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .text {
    width: 100%;
    /* padding: 4vh 0; */
  }
  .text h4 {
    width: 90%;
  }
  #About {
    background: #fff;
    color: #706f6f;
    /* padding: 4vh 0; */
  }
  .text ul li {
    width: 30vw;
    margin: 1vh 0.8vw;
  }
}



