.NavBar {
  display: none;
}

.NavBar img {
  height: 15vh;
  width: 20%;
}

.Navigation {
  width: 80%;
}

.Navigation ul {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Navigation ul li {
  margin: 0 2vw;
  font-size: 1.5em;
  width: 10vw;
}



